.project{
    padding: 15px;
}

.imageContainer {
    position: relative;
    padding: 20px;
    width: 100%;
    border: 1px solid;
    background-size: cover;
    background-position: center;
    box-sizing: border-box;
}

.imageContainer::before {
    position: absolute;
    content: "";
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    background-color: black;
    opacity: 0;
}
.imageContainer:hover::before {
    opacity: 0.3;
}

.projectTitle {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: center;
    font-family: 'Poppins', sans-serif;
    font-size: 20px;
    letter-spacing: 1px;
    margin: 0 0 5px;
    line-height: 1.2;
    color: #ffffff;
}

.description {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: center;
    padding-bottom: 10px;
    font-family: "Open Sans",sans-serif;
    font-size: 14px;
    letter-spacing: 0;
    color: #fcfafa;
}

.button {
    position: relative;
    z-index: 1;
    width: 60px;
    display: flex;
    justify-content: center;
    background-color: #555;
    color: black;
    font-size: 16px;
    padding: 12px 24px;
    border: none ;
    cursor: pointer;
    text-align: center;
}









